import {UniLoadMore} from 'components/uni/uni-load-more/uni-load-more'
import {ApplyDistributorsonlist} from 'api/user'
export default{
	components:{
		UniLoadMore
	},
	data(){
		return{
			index:0,
			tabs:['一级分销','二级分销'],
			page:1,
			pageCount:1,
			moreStatus:"",
			list:[],
			data:{},
			load:false,
			flag:0
		}
	},
	methods:{
		changeTabs(e){
			if(e.tab == '一级分销'){
				this.flag = 0
			}else{
				this.flag = 1
			}
			this.getList()
		},
		getList(p=1){
			this.page = p
			if(this.page==1){
				this.list = []
				this.data = {}
			}
			this.moreStatus = 'loading'
			ApplyDistributorsonlist({
				pageindex:this.page,
				pagesize:10,
				flag:this.flag
			}).then(res=>{
				uni.stopPullDownRefresh();
				this.pageCount = res.pageCount
				if(this.page >= this.pageCount){
					this.moreStatus = 'noMore'
				}else{
					this.moreStatus = 'more'
				}
				if(res.result){
					this.data = res
					this.list = this.list.concat(res.data)
					this.load = true
				}
			})		
		}
	},
	onLoad() {
		this.getList()
	}
}
